table {
  border-color: #545b5e; }

::-webkit-scrollbar {
  background-color: #242423; }

::-webkit-scrollbar-thumb {
  background-color: #0077b6; }

::selection {
  background-color: #ffd166 !important;
  color: #e8e6e3 !important; }

::-moz-selection {
  background-color: #ffd166 !important;
