html {
  font-size: 16px; }
  @media (max-width: 700px) {
    html {
      font-size: 15px; } }

body {
  font-family: Merriweather; }

h1,
h2 {
  font-family: Kanit;
  font-size: 3rem;
  line-height: 2.5rem;
  margin: 0;
  padding: 2rem 0; }
  h1 a,
  h2 a {
    text-decoration: none; }
  @media (max-width: 700px) {
    h1,
    h2 {
      font-size: 2rem;
      padding: 1rem 0; } }

main {
  padding: 0 2rem;
  overflow: hidden;
  margin-top: 0rem;
  display: flex;
  flex-direction: column;
  align-items: center; }

header {
  display: flex;
  justify-content: center; }
  header > div {
    max-width: 76rem;
    flex: 1; }
    header > div h1 {
      text-align: center; }

article.post {
  flex: 1 0 100%;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: calc(75rem - 16rem); }
  article.post .post-meta {
    position: relative; }
    article.post .post-meta .post-title {
      line-height: 3rem; }
  article.post .post-content {
    display: initial; }
    article.post .post-content img {
      max-width: 100%;
      height: auto;
      display: block;
      margin-left: auto;
      margin-right: auto; }
  article.post .post-comments {
    width: 100%; }
  article.post p,
  article.post ul {
    font-size: 1.4375rem;
    line-height: 2.5875rem;
    text-align: justify; }
  @media only screen and (max-width: 61.999rem) {
    article.post {
      max-width: 100%; } }
  @media only screen and (max-width: 29.999rem) {
    article.post {
      max-width: 100%; }
      article.post p,
      article.post ul {
        font-size: 1rem;
        line-height: 2rem; } }

article .post-list {
  flex: 1 0 100%;
  margin: 1rem 0 2rem;
  font-size: 1.4375rem;
  background-color: transparent; }
  @media only screen and (max-width: 29.999rem) {
    article .post-list > a {
      font-size: 1rem; } }
  article .post-list > .post-list-time {
    font-size: 0.9rem;
    font-style: italic;
    font-weight: 100;
    width: 100%;
    display: block;
    text-align: left;
    color: #616161;
    padding: 0.25rem 0; }

footer {
  width: 100%;
  height: 4rem;
  padding: 0 2rem;
  overflow: hidden;
  margin-top: 0rem;
  display: flex;
  flex-direction: column;
  align-items: center; }
  footer > div {
    flex: 1 0 100%;
    justify-content: flex-start;
    padding: 0;
    margin: 0;
    width: 100%;
    max-width: calc(75rem - 16rem);
    display: flex;
    justify-content: center;
    align-items: center; }

blockquote:not([data-name="title"]) {
  border-left: 1rem solid #0077b6;
  font-style: italic;
  margin: 0;
  padding: 0 0 0 2rem;
  position: relative; }
  @media (max-width: 700px) {
    blockquote:not([data-name="title"]) {
      padding: 0 0 0 1rem; } }

code {
  font-family: "Space Mono";
  color: #ef476f; }

pre[class*="language-"] {
  position: relative; }
  @media (max-width: 700px) {
    pre[class*="language-"] {
      font-size: 0.8rem !important; } }
  pre[class*="language-"]:before {
    content: attr(data-language);
    border-bottom-left-radius: 0.25rem;
    color: white;
    font-size: 0.75rem;
    font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    letter-spacing: 0.075em;
    line-height: 1;
    position: absolute;
    right: 0px;
    text-align: right;
    text-transform: uppercase;
    top: 0px;
    z-index: 5;
    background: #1e88e5;
    padding: 0.25rem 0.5rem; }
  pre[class*="language-"].line-numbers.line-numbers code {
    font-family: "Space Mono";
    border-left-color: #3f51b5;
    border-left-width: 2rem; }
  pre[class*="language-"].line-numbers.line-numbers .line-numbers-rows {
    width: 2rem !important;
    border-right-color: #3f51b5; }
    pre[class*="language-"].line-numbers.line-numbers .line-numbers-rows span::before {
      color: #fff;
      text-align: center;
      width: 100%; }
